import axios from "axios"
import React from "react"
import Moment from "moment";
import swal from "sweetalert"



export default function Booking(){



    const [data,set_data]=React.useState([])
    React.useEffect(()=>{
        get_booking()
    },[])


    const get_booking=async()=>{
        let res= await axios.get("/user/booking/list",{ headers: { Authorization: "Bearer " + localStorage.getItem("token") }})
        console.log(res)
        set_data(res.data.data)
    }

    const cancel_booking = async(id)=>{
      let res= await axios.post("/booking/cancel",{booking_id:id},{headers: { Authorization: "Bearer " + localStorage.getItem("token") }})
      if(res.status==200){
        swal("booking cancel.")
      }else{
        swal("Error in Cancel Booking.")

      }
      get_booking()
    }



    return (
        <div className="container">
            
            <header>
        <nav
          className="navbar navbar-expand-lg navbar-dark bg-warning w-100"
          style={{ height: "100px" }}
        >
          <div className="container">
            <div className="d-flex row col-sm-10 col-md-8 col-lg-3">
              <a className="navbar-brand text-dark fonts" href="#">
                First Heritage INN
              </a>
              <span className="subherder">
                400 S Main Street, Kewanee, US star rating
              </span>
            </div>
            <img src="images/logo.jpg" className="img-m" />

            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarResponsive"
              aria-controls="navbarResponsive"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarResponsive">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item active">
                  <a className="nav-link text-dark text-main" href="#">
                    Home
                  </a>
                </li>
                
              </ul>
            </div>
          </div>
        </nav>
      </header>

        <table className="table table-borderless mt-5">
            <tr className="bg-dark text-white">
                <td>Booking Id</td>
                <td>No of Rooms</td>
                <td>From</td>
                <td>To</td>
                <td>status</td>
                <td>Action</td>
            </tr>
            {data.map((data,i)=>{
                return<tr>

            <td>{data.booking_id}</td>
                <td>{data.no_of_rooms}</td>
                <td>{Moment(data.start_date).format("DD-MM-YYY")}</td>
                <td>{Moment(data.end_date).format("DD-MM-YYYY")}</td>
                <td>{data.status}</td>
                <td className="text-danger" onClick={()=>{cancel_booking(data.booking_id)}}>Cancel</td>

                </tr>
            })}
            
        </table>



        </div>
    )




}