import axios from "axios";
import React from "react";
import { AiFillDollarCircle } from "react-icons/ai";
import { BsFillPeopleFill, BsFillBrightnessLowFill } from "react-icons/bs";

export default function Room_details(props) {
  React.useEffect(() => {
    get_api();
  }, []);

  const [datas, set_data] = React.useState([]);

  const get_api = async () => {
    let res = await axios.get(
      `/admin/list/room/id?room_id=${props.location.state.id}`,
      { headers: { Authorization: "Bearer " + localStorage.getItem("admin") } }
    );
    console.log(res.data.data);
    set_data(res.data.data);
  };

  return (
    <div>
      {datas.length == 0
        ? null
        : datas.map((data, i) => {
            return (
              <div  style={{ margin: "auto",
              width: "50%",
              padding: "10px"}}>
                <div className="card card-cascade card-ecommerce wider shadow mb-5 ">
                  <div className="view view-cascade overlay text-center">
                    {" "}
                    <img
                      className="card-img-top"
                      src={data.media}
                      alt=""
                    />{" "}
                    <a>
                      <div className="mask rgba-white-slight"></div>
                    </a>{" "}
                  </div>
                  <div className="card-body card-body-cascade">
                    <h4 className="card-title">
                      <strong style={{ textAlign: "left" }}>
                        <a>{data.room_name}</a>
                      </strong>
                    </h4>

                    <p className="price">
                      <AiFillDollarCircle />
                      {data.amount}
                    </p>
                    <p className="price">{data.no_of_person}</p>
                    <p className="card-text">{data.description}</p>

                    <div>
                      {data.amenities.split(",").map((data) => {
                        return (
                          <span>
                            <BsFillBrightnessLowFill />
                            {"  "}{data}{"  "}
                          </span>
                        );
                      })}
                    </div>

                    <div className="card-footer">
                        <button className="btn btn-danger">Delete</button>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
    </div>
  );
}
