import axios from "axios";
import React from "react";
import {useHistory} from "react-router-dom"
import swal from "sweetalert"


export default function Login(){

    const [username,set_username]=React.useState("")
    const [password,set_password]=React.useState("")
    const history= useHistory()

    const submit = async ()=>{
        try{

        
        let res = await axios.post("/admin/login",{
            email:username,
            password:password
        })
        localStorage.setItem("admin",res.data.token)
        history.push("/admin/home")
    }catch(err){
        console.log(err)
        swal("unable to login please try again.")
    }
    }

    return(
        <div className="container col-4" style={{marginTop:"10%"}}>
        <div className="login-form">
        <div className="main-div">
            <div className="panel">
           <h2 style={{textAlign:"center"}}>Admin Login</h2>
           <p style={{textAlign:"center"}}>Please enter your email and password</p>
           </div>
            <form id="Login" onSubmit={(e)=>{e.preventDefault();submit()}}>
        
                <div className="form-group">
        
        
                    <input type="email" onChange={(e)=>{set_username(e.target.value)}} className="form-control" id="inputEmail" placeholder="Email Address" />
        
                </div>
        
                <div className="form-group">
        
                    <input type="password" onChange={(e)=>{set_password(e.target.value)}} className="form-control" id="inputPassword" placeholder="Password" />
        
                </div>
                <div className="forgot">
                {/* <a href="reset.html">Forgot password?</a> */}
        </div>
                <button type="submit" className="btn btn-primary" style={{textAlign:"center",marginLeft:"40%"}}>Login</button>
        
            </form>
            </div>
        </div></div>
        
    )
}